.profile__info {
  width: calc(100% - 300px);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  margin-right: auto;
}

@media screen and (max-width: 767px) { 
  .profile__info {
    margin: 26px 0 36px;
    width: 100%;
    text-align: center;
  }
}